import React, { Component } from "react";
import{ Grid, Cell } from "react-mdl";

class Landing extends Component {
    render () {
        return (
            <div style={{width: "100%", margin: "auto"}}>
              <Grid className="landing-grid">
                <Cell col={12}>
                    <img
                    style={{margin: "20px"}}
                    width="400" height="300"
                      src="image/a.jpg"
                      alt="avatar"
                      className="avatar-img"
                     />

                     <div className="banner-text">
                       <h1>Front-end Developer </h1>

                       <hr/>
                       <p>A Highway Design Engineer (AutoCAD, AutoCAD Civil 3d) who decided to get into IT realm. JavaScript, ReactJS. And always willing to learn more.</p>

                       <div className="social-links">
                     
                        {/*icons*/} 
                         <a href="https://www.linkedin.com/in/edgar-hovsepyan-6b0800113/"  rel="noopener noreferrer" target="_blank">
                           <i className="fa fa-linkedin-square" aria-hidden="true" id="linkedin" />
                         </a>

                         <a href="https://www.facebook.com/EdgarHovsepyan1987?_rdc=1&_rdr"  rel="noopener noreferrer" target="_blank">
                           <i className="fa fa-facebook-square" aria-hidden="true" id="facebook" />
                         </a>

                         <a href="https://github.com/Edgar201729"  rel="noopener noreferrer" target="_blank">
                           <i className="fa fa-github-square" aria-hidden="true" id="github"/>
                         </a>

                         <a href="https://app.codesignal.com/profile/edgar_hovsepyan" target="_blank">
                           <i className="fa fa-user" id="codesignal" />
                         </a>
                         
                         <a href="https://www.codecademy.com/Edgar_Hovsepyan" target="_blank">
                           <i class="fa fa-code" id="codeacademy"></i>
                         </a>

                       </div>    
                     </div>
                </Cell>
              </Grid>    
            </div>
        )
    }
}

export default Landing;