import React, { Component } from 'react';
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl';

class Contact extends Component {
    render () {
        return (
            <div className="contact-body">
               <Grid className="contact-grid">
                   <Cell col={6}>
                     <h2>Edgar Hovsepyan</h2>
                     <img
              src="image/d.jpg"
              alt="avatar"
              style={{height: '250px'}}
               />
             <p style={{ width: '70%', margin: 'auto', paddingTop: '1em'}}>Self-taught Javascript developer seeks to continue his career in software engineering field. J S fundamentals, ES6, algorithmic thinking, debugging, working with DOM ,OOP skills etc.</p>
                   </Cell>
                   <Cell col={6}>
                   <h2>Contact Me</h2>

                   <hr/>

                   <div className="contact-list">
                   <List>
                     <ListItem>
                       <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                         <i className="fa fa-phone-square" aria-hidden="true"/>
                         (+374) 93-80-40-59
                       </ListItemContent>
                     </ListItem>
                     <ListItem>
                       <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                         <i className="fa fa-fax" aria-hidden="true"/>
                         010 34-73-04
                       </ListItemContent>
                     </ListItem>
                     <ListItem>
                       <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                         <i className="fa fa-envelope" aria-hidden="true"/>
                         edgarhovsepyan1987@gmail.com
                       </ListItemContent>
                     </ListItem>
                     <ListItem>
                       <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                         <i className="fa fa-skype" aria-hidden="true"/>
                         +37493804059
                       </ListItemContent>
                     </ListItem>
                   </List>
                 </div>
                   
                   </Cell>
               </Grid>    
            </div>
        )
    }
}

export default Contact;